'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Link from 'next/link';
import { Plus, Trash2, Search, FileText, AlertCircle, Eye } from 'lucide-react';
import { toast } from 'sonner';

// Komponen UI
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge'; 
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
} from "@/components/ui/alert-dialog";

// Tipe Data
import { Pembelian } from '@/types';

export default function PembelianIndexPage() {
    const [pembelians, setPembelians] = useState<Pembelian[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');
    
    // State untuk Dialog Konfirmasi Void
    const [voidId, setVoidId] = useState<number | null>(null);

    // 1. Fetch Data Riwayat
    const fetchPembelians = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/pembelians');
            setPembelians(response.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat pembelian.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchPembelians(); }, []);

    // Helper Format Rupiah
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    // Helper Format Tanggal
    const formatDate = (dateString: string) => {
        return new Date(dateString).toLocaleDateString('id-ID', {
            day: 'numeric', month: 'short', year: 'numeric'
        });
    };

    // 2. Logika Filter (Pencarian)
    const filteredData = pembelians.filter(item => 
        item.kode_pembelian.toLowerCase().includes(searchQuery.toLowerCase()) ||
        item.supplier?.nama_supplier.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (item.no_faktur_supplier && item.no_faktur_supplier.toLowerCase().includes(searchQuery.toLowerCase()))
    );

    // 3. Handle Void (Batalkan Transaksi)
    const handleVoid = async () => {
        if (!voidId) return;
        
        try {
            await axios.delete(`/api/pembelians/${voidId}`);
            toast.success("Pembelian berhasil dibatalkan & Stok telah dikembalikan.");
            fetchPembelians(); // Refresh data
        } catch (error: any) {
            const msg = error.response?.data?.message || "Gagal membatalkan transaksi.";
            toast.error(msg);
        } finally {
            setVoidId(null); // Tutup dialog
        }
    };

    return (
        <div className="space-y-6">
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Riwayat Pembelian Stok</h1>
                    <p className="text-gray-500 text-sm">Daftar barang masuk dari supplier.</p>
                </div>
                <Link href="/admin/inventory/pembelian/create">
                    <Button className="bg-[#D93F21] hover:bg-[#b9351b]">
                        <Plus className="w-4 h-4 mr-2" /> Catat Pembelian Baru
                    </Button>
                </Link>
            </div>

            {/* Tabel & Filter */}
            <Card className="border border-gray-200 shadow-sm">
                <CardHeader className="pb-3">
                    <div className="flex items-center gap-4">
                        <div className="relative flex-1 max-w-md">
                            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                            <Input 
                                placeholder="Cari No Faktur / Nama Supplier..." 
                                className="pl-9"
                                value={searchQuery}
                                onChange={e => setSearchQuery(e.target.value)}
                            />
                        </div>
                    </div>
                </CardHeader>
                <CardContent>
                    <div className="rounded-md border">
                        <Table>
                            <TableHeader>
                                <TableRow className="bg-gray-50">
                                    <TableHead>Tanggal</TableHead>
                                    <TableHead>Kode / Faktur</TableHead>
                                    <TableHead>Supplier</TableHead>
                                    <TableHead>Total Pembelian</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead className="text-right">Aksi</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {loading ? (
                                    <TableRow>
                                        <TableCell colSpan={6} className="h-24 text-center text-gray-500">
                                            Memuat riwayat...
                                        </TableCell>
                                    </TableRow>
                                ) : filteredData.length === 0 ? (
                                    <TableRow>
                                        <TableCell colSpan={6} className="h-32 text-center text-gray-500">
                                            <div className="flex flex-col items-center justify-center">
                                                <FileText className="h-8 w-8 text-gray-300 mb-2" />
                                                <p>Belum ada data pembelian.</p>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ) : (
                                    filteredData.map((item) => (
                                        <TableRow key={item.id} className="hover:bg-gray-50">
                                            <TableCell className="font-medium">
                                                {formatDate(item.tanggal_pembelian)}
                                            </TableCell>
                                            <TableCell>
                                                <div className="font-semibold text-gray-900">{item.kode_pembelian}</div>
                                                <div className="text-xs text-gray-500">Ref: {item.no_faktur_supplier || '-'}</div>
                                            </TableCell>
                                            <TableCell>
                                                <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                                    {item.supplier?.nama_supplier || 'Unknown'}
                                                </span>
                                            </TableCell>
                                            <TableCell className="font-bold text-gray-900">
                                                {formatRupiah(item.total_harga_faktur)}
                                            </TableCell>
                                            <TableCell>
                                                {item.status === 'selesai' ? (
                                                    <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200 shadow-none">
                                                        Sukses
                                                    </Badge>
                                                ) : (
                                                    <Badge variant="destructive" className="shadow-none">
                                                        Dibatalkan
                                                    </Badge>
                                                )}
                                            </TableCell>
                                            <TableCell className="text-right space-x-1">
                                                {/* Tombol Detail */}
                                                <Link href={`/admin/inventory/pembelian/${item.id}`}>
                                                    <Button variant="ghost" size="sm" className="text-blue-600 hover:text-blue-800 hover:bg-blue-50">
                                                        <Eye className="h-4 w-4" />
                                                    </Button>
                                                </Link>

                                                {/* Tombol Void */}
                                                {item.status === 'selesai' && (
                                                    <Button 
                                                        variant="ghost" 
                                                        size="sm" 
                                                        onClick={() => setVoidId(item.id)} 
                                                        className="text-red-600 hover:text-red-800 hover:bg-red-50"
                                                        title="Batalkan Transaksi (Void)"
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                )}
                                            </TableCell>
                                        </TableRow>
                                    ))
                                )}
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>

            {/* Dialog Konfirmasi Void */}
            <AlertDialog open={!!voidId} onOpenChange={() => setVoidId(null)}>
                <AlertDialogContent>
                    <AlertDialogHeader>
                        <AlertDialogTitle className="text-red-600 flex items-center gap-2">
                            <AlertCircle className="h-5 w-5" /> Batalkan Pembelian?
                        </AlertDialogTitle>
                        <AlertDialogDescription>
                            Tindakan ini akan mengubah status transaksi menjadi <strong>Dibatalkan</strong> dan 
                            secara otomatis <strong>MENGURANGI STOK</strong> bahan baku yang sudah masuk.
                            <br /><br />
                            Pastikan stok fisik masih tersedia sebelum membatalkan.
                        </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                        <AlertDialogCancel>Batal</AlertDialogCancel>
                        <AlertDialogAction onClick={handleVoid} className="bg-red-600 hover:bg-red-700 text-white">
                            Ya, Batalkan Transaksi
                        </AlertDialogAction>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialog>
        </div>
    );
}